<template>
  <div
    class="message"
    @click="openRate"
    :style="{
      marginLeft: isRight ? 'auto' : '0',
      textAlign: isRight ? 'right' : 'left',
    }"
  >
    <div
      class="pcontainer"
      :style="{
        marginLeft: isRight ? 'auto' : '0',
        textAlign: isRight ? 'right' : 'left',
      }"
    >
      <div v-if="rateVisible" class="rating-view justify-content-around">
        <button @click="ratePositive" class="rate-btn">
          <img src="@/assets/like.svg" class="img" alt="" />
        </button>
        <button @click="rateNegative" class="rate-btn">
          <img src="@/assets/dislike.svg" class="img" alt="" />
        </button>
      </div>
      <p
        :style="{
          marginLeft: isRight ? 'auto' : '0',
          backgroundColor: isRight ? '#362FD9' : '#98E4FF',
          color: isRight ? 'white' : 'black',
        }"
      >
        {{ message }}
      </p>
    </div>
  </div>
</template>

<script>
import Api from "@/services/api";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Message",
  props: {
    isRight: Boolean,
    message: String,
    prevMessage: String,
  },
  data() {
    return {
      rateVisible: false,
      api: Object,
    };
  },
  async mounted() {
    this.api = new Api();
    await this.api.setup();
  },
  methods: {
    openRate() {
      this.rateVisible = !this.rateVisible;
    },
    async ratePositive() {
      let data = {
        "message": this.message,
        "previous_message": this.prevMessage,
        "rating": 1,
      };

      const isOk = await this.api.rateMessage(data);
      if (!isOk) {
        alert("error submitting rating");
      }
    },
    async rateNegative() {
      let data = {
        "message": this.message,
        "previous_message": this.prevMessage,
        "rating": 0,
      };
      console.log("rating: ", data);

      const isOk = await this.api.rateMessage(data);
      if (!isOk) {
        alert("error submitting rating");
      }
    },
  },
};
</script>


