}
.rating-view {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(2px);
  display: flex;
  flex-direction: row;
  border-radius: 15px;
}
.pcontainer {
  width: fit-content;
  position: relative;
}
.img {
  height: 100%;
}

