<template>
  <div class="options-container">
    <div class="options-btn-container justify-content-around">
      <button class="btn btn-primary" @click="expand">
        {{
          expanded
            ? "Hide personas & model config"
            : "Show personas & model config"
        }}
      </button>
      <button class="btn btn-info" @click="toggleTheme">Toggle theme</button>
    </div>
    <div v-if="expanded" class="options-details container">
      <div class="row">
        <div class="col-6">
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input @input="modelChangeMale" type="radio" v-model="selectedOptionMale" :value="0" />
            Midnight Enigma
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeMale" v-model="selectedOptionMale" :value="1" />
            Yara
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeMale" v-model="selectedOptionMale" :value="2" />
            Shortwave
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeMale" v-model="selectedOptionMale" :value="3" />
            Kobold godlike
          </label>
          <div v-for="(person, i) in personaMale" :key="i" class="persona">
            <h5 :style="{color: isLightTheme ? 'black' :'white'}">
              {{ person.name }}, {{ person.age }} years,
              {{ person.occupation }}:
            </h5>
            <p :style="{color: isLightTheme ? 'black' :'white'}">{{ person.descriptions }}</p>
            <button :value="i" @click="personaSelectedMale" :disabled="selectedPersonaMale == i" class="btn btn-primary">{{selectedPersonaMale == i ? "Selected" : "Select"}}</button>
          </div>
        </div>
        <div class="col-6">
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeFemale" v-model="selectedOptionFemale" :value=0 />
            Midnight Enigma
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeFemale" v-model="selectedOptionFemale" :value=1 />
            Yara
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeFemale" v-model="selectedOptionFemale" :value=2 />
            Shortwave
          </label>
          <label :style="{color: isLightTheme ? 'black' :'white'}">
            <input type="radio" @input="modelChangeFemale" v-model="selectedOptionFemale" :value=3 />
            Kobold godlike
          </label>
          <div v-for="(person, i) in personaFemale" :key="i" class="persona">
            <h5 :style="{color: isLightTheme ? 'black' :'white'}">
              {{ person.name }}, {{ person.age }} years,
              {{ person.occupation }}:
            </h5>
            <p :style="{color: isLightTheme ? 'black' :'white'}">{{ person.descriptions }}</p>
            <button :value="i" @click="personaSelectedFemale" :disabled="selectedPersonaFemale == i" class="btn btn-primary">{{selectedPersonaFemale == i ? "Selected" : "Select"}}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import personaMaleJson from "@/persona_male.json";
import personaFemaleJson from "@/persona_female.json";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Options",
  data() {
    return {
      expanded: false,
      isLightTheme: true,
      personaMale: Object,
      personaFemale: Object,
      selectedOptionMale: 0,
      selectedOptionFemale: 0,
      selectedPersonaMale: 0,
      selectedPersonaFemale: 0,
    };
  },
  created() {
    this.personaMale = personaMaleJson;
    this.personaFemale = personaFemaleJson;
  },
  methods: {
    toggleTheme() {
      this.isLightTheme = !this.isLightTheme;
      this.$emit("theme-changed", this.isLightTheme);
    },
    expand() {
      this.expanded = !this.expanded;
    },
    personaSelectedMale(e) {
        this.selectedPersonaMale = e.target.value;
        this.$emit("persona-selected-male", e.target.value);
    },
    personaSelectedFemale(e) {
        this.selectedPersonaFemale = e.target.value;
        this.$emit("persona-selected-female", e.target.value);
    },
    modelChangeMale(e) {
        this.$emit("input-changed-male", e.target.value)
    },
    modelChangeFemale(e) {
        this.$emit("input-changed-female", e.target.value)
    }
  },
};
</script>

<style scoped>
.options-btn-container {
  display: flex;
  flex-direction: row;
}
p {
  text-align: left;
}
h5 {
  font-size: 15px;
  text-align: left;
}
.persona {
  border: 1px solid;
}
</style>