<template>
  <div class="container">
    <div class="top-btn-container">
      <button :disabled="generating" class="btn btn-info" @click="generate">
        <span
          v-if="generating"
          class="spinner-border spinner-border-sm"
          role="status"
          aria-hidden="true"
        ></span>
        {{ generating ? "Loading..." : "Generate message" }}
      </button>
      <button
        :disabled="generating"
        class="btn btn-danger"
        @click="regenerateLast"
        :style="{ display: 'none' }"
      >
        <span
          v-if="generating"
          class="spinner-border spinner-border-sm"
          role="status"
          aria-hidden="true"
        ></span>
        {{ generating ? "Loading..." : "Regenerate last message" }}
      </button>
      <button
        :disabled="!firstMsg"
        class="btn btn-info"
        @click="startWithPickupLine"
      >
        Start with pickup line
      </button>
    </div>
    <div class="auto-expand-input">
      <textarea
        class="message-input"
        v-model="message"
        @input="adjustTextareaHeight"
        ref="messageInput"
        placeholder="Type your message..."
        :style="{
          backgroundColor: isLightTheme ? 'white' : '#333333',
          color: isLightTheme ? 'black' : 'white',
        }"
      ></textarea>
    </div>
    <button :disabled="generating" class="btn btn-send" @click="sendMessage">
      <span
        v-if="generating"
        class="spinner-border spinner-border-sm"
        role="status"
        aria-hidden="true"
      ></span>
      {{ generating ? "Loading..." : "Send" }}
    </button>
  </div>
</template>

<script>
import Api from "@/services/api";

export default {
  name: "MessageInput",
  props: {
    isLightTheme: Boolean,
    input: Object,
  },
  data() {
    return {
      message: "",
      api: Object,
      generating: false,
      lastIsLlama: true,
      firstMsg: true,
    };
  },
  async mounted() {
    this.api = new Api();
    await this.api.setup();
  },
  methods: {
    adjustTextareaHeight() {
      const textarea = this.$refs.messageInput;
      textarea.style.height = "auto";
      textarea.style.height = textarea.scrollHeight + "px";
    },
    async sendMessage() {
      if (this.generating) {
        return;
      }

      const msg = this.message;
      this.$emit("message-sent", this.message);
      this.message = ""; // Clear the message input after sending
      this.$nextTick(() => {
        const textarea = this.$refs.messageInput;
        textarea.style.height = "60px";
      });

      this.generating = true;
      this.firstMsg = false;

      let config = {
        message: msg,
        male: this.input.personaMale,
        female: this.input.personaFemale,
        model_config_male: this.input.modelConfigMale,
        model_config_female: this.input.modelConfigFemale,
      };

      const reply = await this.api.sendMessage(config);
      this.$emit("reply-received", reply);
      this.generating = false;
      this.lastIsLlama = true;
    },
    async generate() {
      if (this.generating || !this.lastIsLlama) {
        return;
      }

      console.log("generating...");
      this.generating = true;
      this.firstMsg = false;
      let config = {
        male: this.input.personaMale,
        female: this.input.personaFemale,
        model_config_male: this.input.modelConfigMale,
        model_config_female: this.input.modelConfigFemale,
        generate: true,
      };
      const reply = await this.api.generateMessage(config);
      this.message = reply;
      this.adjustTextareaHeight();
      this.generating = false;
      this.lastIsLlama = false;
    },
    async regenerateLast() {
      if (this.generating) {
        return;
      }
      console.log("regenerating...", this.lastIsLlama);
      this.generating = true;
      this.firstMsg = false;

      let config = {
        male: this.input.personaMale,
        female: this.input.personaFemale,
        model_config_male: this.input.modelConfigMale,
        model_config_female: this.input.modelConfigFemale,
        generate: !this.lastIsLlama,
        regenerate: true,
      };

      const reply = await this.api.regenerateLast(config);

      this.generating = false;

      if (this.lastIsLlama == false) {
        this.message = reply;
      } else {
        this.$emit("regenerate", reply);
      }
    },
    async startWithPickupLine() {
      if (!this.firstMsg) {
        return;
      }
      this.generating = true;
      this.firstMsg = false;
      const pickupLine = await this.api.startWithPickupLine();
      if (pickupLine != "") {
        this.$emit("pickup", pickupLine);
      }
      this.generating = false;
    }
  },
};
</script>

<style scoped>
.container {
  position: relative;
  padding: 0;
}
.auto-expand-input {
  width: 100%;
  margin-left: 0;
}

.message-input {
  width: 100%;
  height: 60px;
  border-radius: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #333333;
  color: white;
}

.btn-send {
  height: 40px;
  width: 80px;
  position: absolute;
  bottom: 15px;
  right: 15px;
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.btn-info {
  margin-bottom: 5px;
  margin-right: 5px;
}
.btn-danger {
  margin-bottom: 5px;
  margin-left: 5px;
}

textarea {
  resize: none;
  min-height: 2rem;
  border: 1px solid #ccc;
  padding: 0.5rem;
  font-size: 1rem;
  outline: none;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 90%;
  }
}
</style>
