<template>
  <div class="main-container">
    <div class="content container">
      <Options
        @theme-changed="handleTheme"
        @input-changed-male="handleMaleInput"
        @input-changed-female="handleFemaleInput"
        @persona-selected-male="handleMalePersona"
        @persona-selected-female="handleFemalePersona"
      />
      <MessageContainer :messages="messages" />
    </div>
    <MessageInput
      :isLightTheme="isLightTheme"
      :input="input"
      class="msg-input"
      @message-sent="messageSent"
      @reply-received="replyReceived"
      @regenerate="regenerate"
      @pickup="handlePickupLine"
    />
  </div>
</template>

<script>
import MessageContainer from "./components/MessageContainer.vue";
import MessageInput from "./components/MessageInput.vue";
import Options from './components/Options.vue';

export default {
  name: "App",
  components: {
    MessageInput,
    MessageContainer,
    Options,
  },
  data() {
    return {
      isLightTheme: true,
      input: {
        modelConfigMale: 0,
        modelConfigFemale: 0,
        personaMale: 0,
        personaFemale: 0
      },
      messages: [
        {
          "llama": true,
          "message": "hey how r u doin"
        }
      ]
    }
  },
  methods: {
    messageSent(message) {
      this.messages.push({
        "llama": false,
        "message": message
      })
      this.messages.push({
        "llama": true,
        "message": "Typing..."
      })
    },
    replyReceived(message) {
      this.messages.pop();
      this.messages.push({
        "llama": true,
        "message": message
      })
    },
    regenerate(message) {
      this.messages.pop();
      this.messages.push({
        "llama": true,
        "message": message
      })
    },
    handlePickupLine(pickupLine) {
      this.messages[0] = {
        "llama": true,
        "message": pickupLine
      }
    },
    handleTheme(isLight) {
      console.log("Theme")
      document.body.style.backgroundColor = isLight ? 'white' : 'black';
      this.isLightTheme = isLight;
    },
    handleMaleInput(model) {
      console.log("input male change: ", model)
      this.input.modelConfigMale = parseInt(model);
    },
    handleFemaleInput(model) {
      console.log("input female change: ", model)
      this.input.modelConfigFemale = parseInt(model);
    },
    handleMalePersona(persona) {
      this.input.personaMale = parseInt(persona);
    },
    handleFemalePersona(persona) {
      this.input.personaFemale = parseInt(persona);
    }
  }
};
</script>

<style>
.main-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100vh !important;
}
.content {
  flex: 1;
}
.msg-input {
  position: sticky;
  bottom: 30px;
}

@media screen and (max-width: 767px) {
  .content {
    max-width: 100%;
  }
}
</style>