import axios from "axios";

class Api {
    url

    constructor() {}

    setup = async () => {
        const configres = await fetch('/config.json');
        const config = await configres.json();

        axios.defaults.baseURL = config.baseUrl;

        console.log("base url: ", axios.defaults.baseURL);
    }

    sendMessage = async (data) => {
        const res = await axios.post("/gpt/message", data);
        console.log("res: ", res)
        if (res.data.status == "ok") {
            return res.data.response
        }
        return "";
    }

    generateMessage = async (data) => {
        const res = await axios.post(`/gpt/message`, data);
        console.log("res: ", res)
        if (res.data.status == "ok") {
            return res.data.response;
        }
        return "";
    }

    regenerateLast = async (data) => {
        const res = await axios.post(`/gpt/message`, data);
        console.log("res: ", res)
        if (res.data.status == "ok") {
            return res.data.response;
        }
        return "";
    }

    rateMessage = async (data) => {
        const res = await axios.post("https://dategpt.chat/api/rate", data);
        console.log("res: ", res)
        if (res.data.status == "ok") {
            return true;
        } else if (res.data.error != undefined) {
            console.log("res error: ", res.data.error);
            return false;
        }
    }

    startWithPickupLine = async () => {
        const res = await axios.post("/gpt/pickup");
        console.log("Res: ", res);

        if (res.data.status == "ok") {
            return res.data.pickup;
        } else if (res.data.error != undefined) {
            console.log("res error: ", res.data.error);
            return "";
        }
    }

}

export default Api;